const {Component} = Shopware;

import template from './index.html.twig';
import './index.scss';

Component.register('rhweb-section', {
    template,

    props: {
        value: {
            type: Object,
            required: true
        },
    },

    data() {
        return {
            snippetPrefix: 'rhweb-section.',
            showRhwebSectionSettings: false,
        };
    },

    methods:{
        onRhwebSettingsButtonClick() {
            this.showRhwebSectionSettings = true;
        },

        onRhwebCloseSettingsModal() {
            this.showRhwebSectionSettings = false;
        }
    },

    computed: {
        separatorOptions() {
            return [
                'none',
                'scratch',
                'toothed',
                'waves',
                'papercut',
                'clouds',
                'paper-ripped'
            ];
        },

        offsetOptions() {
            return [
                'none',
                '50',
                '100',
                '150',
                '200'
            ];
        },

        colorOptions() {
            return [
                'none',
                'primary',
                'secondary',
                'light',
                'white',
                'dark',
                'text-color',
                'background-color'
            ];
        },

        filterOptions() {
            return [
                'none',
                'grayscale',
                'blur'
            ];
        }
    },

    created() {
        console.log("created");
        console.log(this.value);
    },
});
