import template from './sw-cms-section-config.html.twig';

const {Component} = Shopware;

Component.override('sw-cms-section-config', {
    template,

    created() {
        if (!this.section.customFields) {
            this.$set(this.section, 'customFields', {});
        }

        if (!this.section.customFields.rhweb_section) {
            this.$set(this.section.customFields, 'rhweb_section', {
                color: null,
                backgroundColor: null,
                backgroundFilter: null,
                fixedBackground: null,
                top: null,
                bottom: null,
                offsetTop: null,
                offsetBottom: null
            });
        }
    }
});
