const Application = Shopware.Application;
import './component';
import './config';
import './preview';

const Criteria = Shopware.Data.Criteria;
const criteria = new Criteria();
criteria.addAssociation('cover');

Application.getContainer('service').cmsService.registerCmsElement({
    name: 'rhweb-deal-counter',
    label: 'rhweb-cms.elements.dealCounter.label',
    component: 'sw-cms-el-rhweb-deal-counter',
    configComponent: 'sw-cms-el-config-rhweb-deal-counter',
    previewComponent: 'sw-cms-el-preview-rhweb-deal-counter',
    defaultConfig: {
        product: {
            source: 'static',
            value: null,
            required: true,
            entity: {
                name: 'product',
                criteria: criteria
            }
        },
        oldPrice: {
            source: 'static',
            value: null,
        },
        newPrice: {
            source: 'static',
            value: null,
        },
        startDateTime: {
            source: 'static',
            value: null,
        },
        endDateTime: {
            source: 'static',
            value: null
        },
        endTimerHide:{
            source: 'static',
            value: true
        },
        showBeforeStart: {
            source: 'static',
            value: true
        },
        teaserText: {
            source: 'static',
            value: ""
        },
        showBadge: {
            source: 'static',
            value: true
        },
        badgeText: {
            source: 'static',
            value: "Sale %"
        },
        activateCustomDescription: {
            source: 'static',
            value: true
        },
        customDescription: {
            source: 'static',
            value: ""
        },
        activateCustomCss:{
            source: 'static',
            value: false
        },
        customCss:{
            source: 'static',
            value: ""
        }
    },
    collect: function collect(elem) {
        const context = Object.assign(
            {},
            Shopware.Context.api,
            { inheritance: true }
        );

        const criteriaList = {};

        Object.keys(elem.config).forEach((configKey) => {
            if (elem.config[configKey].source === 'mapped') {
                return;
            }

            const entity = elem.config[configKey].entity;

            if (entity && elem.config[configKey].value) {
                const entityKey = entity.name;
                const entityData = {
                    value: [elem.config[configKey].value],
                    key: configKey,
                    searchCriteria: entity.criteria ? entity.criteria : new Criteria(),
                    ...entity
                };

                entityData.searchCriteria.setIds(entityData.value);
                entityData.context = context;

                criteriaList[`entity-${entityKey}`] = entityData;
            }
        });

        return criteriaList;
    }
});
