const { Component, Mixin } = Shopware;
const Criteria = Shopware.Data.Criteria;
import template from './index.html.twig';

Component.register('sw-cms-el-rhweb-config-manufacturer-list', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    inject: [

    ],

    data() {
        return {
            snippetPrefix: 'sw-cms.elements.rhweb-manufacturer-list.config.'
        };
    },

    computed: {
        defaultCriteria() {
            const criteria = new Criteria();
            return criteria;
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('rhweb-manufacturer-list');
            this.initElementData('rhweb-manufacturer-list');
        }
    }
});
