const Application = Shopware.Application;
import './component';
import './config';
import './preview';

const Criteria = Shopware.Data.Criteria;
const criteria = new Criteria();
criteria.addAssociation('cover');

Application.getContainer('service').cmsService.registerCmsElement({
    name: 'rhweb-shop-the-look',
    label: 'rhweb-cms.elements.shopTheLook.label',
    component: 'sw-cms-el-rhweb-shop-the-look',
    configComponent: 'sw-cms-el-config-rhweb-shop-the-look',
    previewComponent: 'sw-cms-el-preview-rhweb-shop-the-look',
    defaultConfig: {
        products: {
            source: 'static',
            value: [],
            required: true,
            entity: {
                name: 'product',
                criteria: criteria
            }
        },
        media: {
            source: 'static',
            value: null,
            required: true,
            entity: {
                name: 'media'
            }
        },
        productMediaHotspots: {
            source: 'static',
            value: {}
        },
        style: {
            source: 'static',
            value: 'standard'
        }
    }
});
