const { Component, Mixin } = Shopware;
const { Criteria, EntityCollection } = Shopware.Data;

import template from './index.html.twig';
import './index.scss';

Component.register('sw-cms-el-config-rhweb-deal-counter', {
    template,

    inject: ['repositoryFactory'],

    mixins: [
        Mixin.getByName('cms-element')
    ],

    data() {
        return {
            snippetPrefix: 'sw-cms.elements.rhweb-deal-counter.config.',
            product: null,
        };
    },

    computed: {
        productRepository() {
            return this.repositoryFactory.create('product');
        },

        productSelectContext() {
            const context = Object.assign({}, Shopware.Context.api);
            context.inheritance = true;

            return context;
        },

        product() {
            if (this.element.data && this.element.data.product) {
                return this.element.data.product;
            }

            return null;
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('rhweb-deal-counter');
        },

        updatePrice(productId) {
            const criteria = new Criteria();
            criteria.addAssociation('cover');

            this.productRepository.get(productId, this.productSelectContext, criteria).then((product) => {
                this.product = product;
                this.element.config.product.value = this.product.id;

                if(this.product.price[0].listPrice.gross > this.product.price[0].gross){
                    this.element.config.oldPrice.value = this.product.price[0].listPrice.gross;
                }
                else{
                    this.element.config.oldPrice.value = this.product.price[0].gross;
                }

                this.element.config.newPrice.value = this.product.price[0].gross;
                this.$set(this.element.data, 'product', this.product);
            });
        },
    }
});
