const { Component, Mixin } = Shopware;
import template from './index.html.twig';

import './index.scss';

Component.register('sw-cms-el-rhweb-placeholder', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('rhweb-placeholder');
            this.initElementData('rhweb-placeholder');
        }
    }
});