const {Application, Component, Mixin} = Shopware;
import template from './index.html.twig';

Component.register('sw-cms-el-rhweb-manufacturer-list', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    computed: {
        navCss() {
            if (this.element.config.activateNav.value) {
                return {
                    'display': 'flex'
                }
            }
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('rhweb-manufacturer-list');
            this.initElementData('rhweb-manufacturer-list');
        }
    }
});
