const { Application } = Shopware;
const Criteria = Shopware.Data.Criteria;
const criteria = new Criteria();

import './component';
import './config';
import './preview';

Application.getContainer('service').cmsService.registerCmsElement({
    name: 'rhweb-manufacturer-list',
    label: 'rhweb-cms.elements.manufacturerList.label',
    component: 'sw-cms-el-rhweb-manufacturer-list',
    configComponent: 'sw-cms-el-rhweb-config-manufacturer-list',
    previewComponent: 'sw-cms-el-rhweb-preview-manufacturer-list',
    defaultConfig: {
        activateNav: {
            source: 'static',
            value: true
        },
        navAnchorPrefix: {
            source: 'static',
            value: 'anchor'
        },
        activateUrls: {
            source: 'static',
            value: true
        },
        activateUrlsNewTab: {
            source: 'static',
            value: false
        },
        activateImages: {
            source: 'static',
            value: true
        },
        imageHeight: {
            source: 'static',
            value: '60px'
        },
        imageMaxWidth: {
            source: 'static',
            value: '150px'
        },
        manufacturerBackground: {
            source: 'static',
            value: '#f2f2f2'
        },
        manufacturerColor: {
            source: 'static',
            value: '#444444'
        },
    }
});
