import './component';
import './preview';

Shopware.Service('cmsService').registerCmsBlock({
    name: 'rhweb-cta-banner-basic',
    label: 'rhweb-cms.blocks.ctaBannerbasic.label',
    category: 'rhweb-cms-cta',
    component: 'sw-cms-block-rhweb-cta-banner-basic',
    previewComponent: 'sw-cms-preview-rhweb-cta-banner-basic',
    defaultConfig: {
        marginBottom: '20px',
        marginTop: '20px',
        marginLeft: '20px',
        marginRight: '20px',
        sizingMode: 'boxed'
    },
    slots: {
        one: {
            type: 'rhweb-cta-banner',
            default: {
                config: {
                    elementType: {
                        source: 'static',
                        value: 'category'
                    },
                    height: {
                        source: 'static',
                        value: '250px'
                    },
                    mediaActive: {
                        source: 'static',
                        value: false
                    },
                    videoActive: {
                        source: 'static',
                        value: false
                    },
                }
            }
        }
    }
});
