const { Component, Application, Mixin } = Shopware;
import template from './index.html.twig';
import './index.scss';

Component.register('sw-cms-el-rhweb-deal-counter', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    computed: {

        product() {
            if (!this.element.data || !this.element.data.product) {
                return {
                    name: 'Lorem ipsum dolor',
                    description: `Lorem ipsum dolor sit amet, consetetur sadipscing elitr,
                    sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat,
                    sed diam voluptua.`.trim(),
                    ratingAverage: 5,
                    price: [
                        { gross: 19.90 }
                    ],
                    cover: {
                        media: {
                            url: '/administration/static/img/cms/preview_glasses_large.jpg',
                            alt: 'Lorem Ipsum dolor'
                        }
                    }
                };
            }

            return this.element.data.product;
        },

        mediaUrl() {
            const context = Shopware.Context.api;

            if (this.product.cover && this.product.cover.media) {
                if (this.product.cover.media.id) {
                    return this.product.cover.media.url;
                }

                return `${context.assetsPath}${this.product.cover.media.url}`;
            }

            return `${context.assetsPath}/administration/static/img/cms/preview_glasses_large.jpg`;
        },

    },

    watch: {
        cmsPageState: {
            deep: true,
            handler() {
                this.$forceUpdate();
            }
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('rhweb-deal-counter');
            this.initElementData('rhweb-deal-counter');
        }
    },

    filters: {
        numberFormat: function (value) {
            return new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'EUR' }).format(value)
        }
    }
});
