const { Component, Application, Mixin } = Shopware;
import template from './index.html.twig';
import './index.scss';

Component.register('sw-cms-el-rhweb-cta-banner', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    computed: {
        category() {
            if (!this.element.data || !this.element.data.category || !this.element.data.category.media) {
                return {
                    name: 'Lorem ipsum dolor',
                    description: `Lorem ipsum dolor sit amet, consetetur sadipscing elitr,
                    sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat,
                    sed diam voluptua.`.trim(),
                    media: {
                        url: '/administration/static/img/cms/preview_glasses_large.jpg',
                        alt: 'Lorem Ipsum dolor'
                    }
                };
            }
            return this.element.data.category;
        },
        mediaUrl() {
            const context = Shopware.Context.api;
            const elemData = this.element.data.media;

            if (this.element.config.elementType.value != 'usp' && this.element.config.elementType.value != 'custom'){
                if (!this.element.config.mediaActive.value) {
                    if (this.element.config.elementType.value == 'category') {
                        if (this.category.media && this.category.media.id) {
                            return this.category.media.url;
                        }
                        return `${context.assetsPath}${this.category.media.url}`;
                    }
                }
                if (elemData && elemData.id) {
                    return this.element.data.media.url;
                }
                if (elemData && elemData.url) {
                    return `${context.assetsPath}${elemData.url}`;
                }
                if (this.element.config.videoActive.value) {
                    return 'https://rh-webdesign.com/wp-content/uploads/2020/03/sw6-example-video.mp4';
                }
                return `${context.assetsPath}/administration/static/img/cms/preview_mountain_large.jpg`;
            }
        },
        elementCss() {
            if ((this.element.config.videoActive.value || this.element.config.mediaActive.value)) {
                if (this.element.config.elementType.value == 'usp' || this.element.config.elementType.value == 'custom') {
                    return {
                        'background-image': 'none',
                        'height': this.element.config.elementHeight.value
                    }
                }
                else {
                    return {
                        'background-image': 'url(' + this.mediaUrl + ')',
                        'height': this.element.config.elementHeight.value
                    }
                }
            }
            else {
                return {
                    'background-image': 'url(' + this.mediaUrl + ')',
                    'height': this.element.config.elementHeight.value
                }
            }
        },
        overlayCss() {
            return {
                'align-items': this.element.config.boxVerticalAlign.value,
                'justify-content': this.element.config.boxHorizontalAlign.value,
                'height': this.element.config.elementHeight.value
            }
        },
        boxCss() {
            if (this.element.config.elementType.value == 'custom'){
                return {
                    'color': 'inherit',
                    'text-align': this.element.config.boxTextAlign.value,
                    'background': 'none'
                }
            }
            if(this.element.config.elementType.value == 'usp'){
                return {
                    'color': this.element.config.boxColor.value,
                    'text-align': this.element.config.boxTextAlign.value,
                    'background': 'none'
                }
            }
            if(this.element.config.elementType.value == 'cta' || this.element.config.elementType.value == 'category'){
                return {
                    'color': this.element.config.boxColor.value,
                    'text-align': this.element.config.boxTextAlign.value,
                    'background': this.element.config.elementBackground.value,
                    'min-height': this.element.config.boxHeight.value,
                    'max-width': this.element.config.captionMaxWidth.value,
                }
            }

        },
        headlineCss() {
            return {
                'color': this.element.config.boxHeadlineColor.value
            }
        }
    },

    watch: {
        cmsPageState: {
            deep: true,
            handler() {
                this.$forceUpdate();
            }
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('rhweb-cta-banner');
            this.initElementData('rhweb-cta-banner');
        }
    }
});
